import { unified } from "unified";
import rehypeParse from "rehype-parse";
import App from "./App.svelte";
const omitDeep = require("omit-deep-lodash");

document.querySelectorAll(".oissu").forEach((element) => {
    const ast = omitDeep(
        unified()
            .use(rehypeParse, {
                fragment: true,
                verbose: false,
            })
            .parse(element.outerHTML),
        "position"
    );
    console.debug(ast);
    console.debug(ast);
    element.innerHTML = "";
    new App({
        target: element,
        props: {
            ast: ast,
        },
    });
    // }
});

